import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CashierPageComponent } from './cashier/cashier.component';
import { SalePageComponent } from './sale.component';

const routes: Routes = [
  {
    path: '',
    component: SalePageComponent,
    children: [
      {
        path: '',
        component: CashierPageComponent,
      },
      {
        path: 'cashier',
        component: CashierPageComponent,
      },
      {
        path: 'history',
        loadChildren: () => import('./history-sale/history-sale.module')
          .then(module => module.HistorySalePageModule)
      },
      {
        path: 'price-list',
        loadChildren: () => import('./price-list/price-list.module')
          .then(module => module.PriceListPageModule)
      }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SaleRoutingModule { }
